import React from 'react'
import { connect } from 'react-redux'
import { editState } from '../actions/appState'


class W19 extends React.Component {

  componentDidMount() {
    this.props.editState('true', 'landingPage')
  }

  render() {
    return (
      <>
        <div className='backgroundBox'></div>
        <div className='topSpace'>
          <div className='border'>
            <div className='item'>
              <div className='item-title'>
                <div className='item-box'><h1>Wawrzyca 19</h1></div>
              </div>
              <img className='item-img' src='img/w19-01.jpg' alt='w19' />
              <div className='item-description right'>
                <div className='item-description-box'>
                W latach 2015 - 2017 zabudowania dawnej Elektrownia Miejskiej przy ul. w. Wawrzyca 19 zostay poddane przebudowie i zmianie sposobu uytkowania na kompleks apartamentw z wydzielon czci usugow. 
              </div>
              </div>
            </div>
          </div>

          <div className='border'>
            <div className='item'>
              <img className='item-img' src='img/w19-02.jpg' alt='w19' />
              <div className='item-description left'>
                <div className='item-description-box'>
                Zaoeniem inwestora byo stworzenie w zabytkowej, postindustrialnej przestrzeni budynku mieszkalnego o wysokim standardzie i unikalnym charakterze. Przebudowa obja rekonstrukcj zabytkowej zabudowy, przywracajc i eksponujc pierwotny wygld elewacji hal dawnej elektrowni autorstwa Jana Rzymkowskiego.
              </div>
              </div>
            </div>
          </div>

          <div className='border'>
            <div className='item'>
              <img className='item-img' src='img/w19-03.jpg' alt='w19' />
              <div className='item-description right'>
                <div className='item-description-box'>
                Adaptacja niepowtarzalnych, poprzemysowych przestrzeni, wykorzystanie elementw metalowych konstrukcji, sklepie czy nietypowych okien, pozwolia architektom na zastosowanie oryginalnych rozwiza i stworzenie budynku o wyjtkowym klimacie, czcym industrialn estetyk z funkcjonalnoci.
              </div>
              </div>
            </div>
          </div>

          <div className='border'>
            <div className='item'>
              <img className='item-img' src='img/w19-04.jpg' alt='w19' />
              <div className='item-description left'>
                <div className='item-description-box'>
                Prace budowlane prowadzono w taki sposb, aby zachowa jak najwicej zabytkowej substancji i oryginalnych elementw w celu ochrony dziedzictwa kulturowego. Przeprowadzano konserwacj midzy innymi ceglanej fasady hali kotowni z 1914 roku i stalowej konstrukcji wsporczej silosu na wgiel, ktra zostaa wykorzystana do podparcia szklanego zadaszenia na dziedzicu. 

              </div>
              </div>
            </div>
          </div>

        </div>
      </>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    appState: state.appState
  }
}

export default connect(mapStateToProps, { editState })(W19)